.post {
    display: grid;
    grid-template-areas:
". img message ."
". like message .";
    grid-template-columns: 1fr 1fr 15fr 1fr;
    grid-template-rows: 1fr 1fr;
    color: azure;
    margin: 0 auto auto auto;
}

.img {
    grid-area: img;
    float: left;
    /*    grid-column: span 1;*/
    height: 50px;
    border-radius: 50%;
    margin-left: 20px;
}

.messageBlock {
    grid-area: message;
    width: 100%;
    margin-right: 20px;
    color: black;
    margin-top: 25px;
    margin-left: 1%;
}

.message {
    color: black;
    background-color: azure;
    border-radius: 0 10px 10px 10px;
    border: solid 2px black;
    padding: 10px;
}

.like {
    grid-area: like;
    margin-left: 20px;
}